<template>
  <div id="Stock" class="tabcontent">
    <h2 style="text-align: center">Pasteles</h2>
    <div class="table-responsive">
      <table class="table stock_table">
        <thead>
          <tr class="text-center">
            <th class="text-center">ID</th>
            <th class="text-center">Nombre</th>
            <th class="text-center">Precio</th>
            <th class="text-center">Cantidad</th>
            <th class="text-center">Imagen</th>
            <th class="text-center">Acciones</th>
          </tr>
        </thead>
        <tbody>
          <tr class="text-center" v-for="pastel in pasteles">
            <td>{{ pastel.id }}</td>
            <td>{{ pastel.nombre }}</td>
            <td>{{ formatCurrency(pastel.precio) }}</td>
            <td>{{ pastel.cantidad }}</td>
            <td><img :src="pastel.img_src" class="stock_img" /></td>
            <td><a href="#">Editar</a> | <a href="#">Eliminar</a></td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>
<script>
export default {
  name: "StockPasteles",
  computed: {
    pasteles() {
      return this.$store.state.sabores;
    },
  },
  methods: {
    formatCurrency(value) {
      let val = (value / 1).toFixed(2)
      return '$ ' + val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
    }
  }
};
</script>
