<template>
  <div id="Ordenes" class="tabcontent">
    <h2 style="text-align: center">Odenes pendientes</h2>
    <div class="table-responsive">
      <table class="table">
        <thead>
          <tr class="text-center">
            <th class="text-center">Orden</th>
            <th class="text-center">Cliente</th>
            <th class="text-center">Telefono</th>
            <th class="text-center">Email</th>
            <th class="text-center">Pasteles</th>
            <th class="text-center">Adornos</th>
            <th class="text-center">Complementos</th>
            <th class="text-center">Importe</th>
            <th class="text-center">Surtido</th>
            <th class="text-center">Acciones</th>
          </tr>
        </thead>
        <tbody>
          <tr class="text-center" v-for="orden in ordenes">
            <td>{{ orden.id }}</td>
            <td>{{ orden.cliente }}</td>
            <td>{{ orden.telefono }}</td>
            <td>{{ orden.email }}</td>
            <td>{{ remove_brackets(orden.pasteles) }}</td>
            <td>
              <div v-if="orden.adornos.length > 0">
                {{ remove_brackets(orden.adornos) }}
              </div>
              <div v-else>No</div>
            </td>
            <td>
              <div v-if="orden.complementos.length > 0">
                {{ remove_brackets(orden.complementos) }}
              </div>
              <div v-else>No</div>
            </td>
            <td>{{ formatCurrency(orden.importe) }}</td>
            <td>{{ orden.surtido }}</td>
            <td>
              <div v-if="orden.surtido == false">
                <button type="button" class="btn btn-sm btn-secondary">
                  Surtir
                </button>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>
<script>
export default {
  name: "Ordenes",
  computed: {
    ordenes() {
      return this.$store.state.ordenes;
    },
  },
  methods: {
    remove_brackets: function (arr) {
      let arreglo = arr.toString();
      return arreglo.replace(/[\[\]']+/g, "");
    },
    formatCurrency(value) {
      let val = (value / 1).toFixed(2)
      return '$ ' + val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
    }
  },
};
</script>
