<template>
  <div class="box-body">
    <div class="acciones">
      <div class="seccion_accion">
        <a class="comprar" @click="show_pasteles()">Stock Pasteles</a>
      </div>
      <div class="seccion_accion">
        <a class="comprar" @click="show_adornos()">Stock Adornos</a>
      </div>
      <div class="seccion_accion">
        <a class="comprar" @click="show_complementos()">Stock Complementos</a>
      </div>
      <div class="seccion_accion">
        <a class="comprar" @click="show_ordenes()"> Ordenes </a>
      </div>
    </div>
    <div id="stock_pasteles">
      <StockPasteles />
    </div>
    <div id="stock_adornos" style="display: none">
      <StockAdornos />
    </div>
    <div id="stock_complementos" style="display: none">
      <StockComplementos />
    </div>
    <div id="seccion_ordenes" style="display: none">
      <Ordenes />
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import Ordenes from "@/components/pastelero_ordenes.vue";
import StockPasteles from "@/components/stock_pasteles.vue";
import StockAdornos from "@/components/stock_adornos.vue";
import StockComplementos from "@/components/stock_complementos.vue";
import "@/assets/css/estilos_pasteleria.css";

export default {
  name: "HomeView",
  components: {
    Ordenes,
    StockPasteles,
    StockAdornos,
    StockComplementos
  },
  methods: {
    show_pasteles: function () {
      $("#seccion_ordenes").hide();
      $("#stock_adornos").hide();
      $("#stock_complementos").hide();
      $("#stock_pasteles").show();
    },
    show_adornos: function () {
      $("#seccion_ordenes").hide();
      $("#stock_complementos").hide();
      $("#stock_pasteles").hide();
      $("#stock_adornos").show();
    },
    show_complementos: function () {
      $("#seccion_ordenes").hide();
      $("#stock_pasteles").hide();
      $("#stock_adornos").hide();
      $("#stock_complementos").show();
    },
    show_ordenes: function () {
      $("#stock_pasteles").hide();
      $("#stock_adornos").hide();
      $("#stock_complementos").hide();
      $("#seccion_ordenes").show();
    },
  },
};
</script>
